@import "_normalize";
@import "_variables";
@import "navigation";
@import "abaut";
@import "slow_food";
@import "menu_card";
@import url("https://fonts.googleapis.com/css?family=Lobster|Staatliches&display=swap&subset=latin-ext");
@import url("https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap&subset=latin-ext");

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  background-color: darkgray;

  -webkit-backdrop-size: cover;
  background-size: cover;
  background-position: center center;
  height: 100vh;
}
